// import b2cFooter from "../../components/b2cfooter.vue";
export default {
	components: {
		// b2cFooter
	},
	data() {
		return {
			pageData: {},
			pageLoad: false,
			keyword: ""
		}
	},
	onLoad: function(ops) {
		uni.setNavigationBarTitle({
			title: "买菜宝"
		})
		// this.getPage();
		this.getPage()
	},
	methods: {
		search: function() {

		},
		goProduct: function(id) {
			console.log(id)
			uni.navigateTo({
				url:"../product/product?id="+id
			})
		},
		onAdd: function() {
			const db = wx.cloud.database()
			db.collection('goods').add({
				data: {
					count: 1
				},
				success: res => {
					// 在返回结果中会包含新创建的记录的 _id
					this.setData({
						counterId: res._id,
						count: 1
					})
					wx.showToast({
						title: '新增记录成功',
					})
					console.log('[数据库] [新增记录] 成功，记录 _id: ', res._id)
				},
				fail: err => {
					wx.showToast({
						icon: 'none',
						title: '新增记录失败'
					})
					console.error('[数据库] [新增记录] 失败：', err)
				}
			})
		},
		getPage: function() {
			wx.cloud.init()
			const db = wx.cloud.database()
			db.collection('goods').get({
				success: res => {
					console.log(res);
					this.pageData = res.data
					this.pageLoad = true;
					// this.pageLoad = true;表示数据完成可以渲染
				},
				fail: err => {
					wx.showToast({
						icon: 'none',
						title: '查询记录失败'
					})
					console.error('[数据库] [查询记录] 失败：', err)
				}
			})
		},
		test: function() {
			wx.cloud.init() //调用前需先调用init
			wx.cloud.callFunction({
				name: 'check',
				data: {
					"content": this.contents
				}
			}).then(res => {
				console.log(res.result)
				if (!res.result.code) {
					uni.showModal({
						title: "温馨提示",
						content: "你所输入的内容可能含有违法违规内容，不支持进行下一步操作"
					})
					return
				} else {
					// 你要进行的操作
				}
			})
		}
	}
}
